#contact-form {
  margin: 0 auto;
  background-image: url('/img/jul.jpg');
  background-position: 250% 30%;
  background-repeat: no-repeat;
  background-size: 800px auto;
  width: 80%;
}
#gradient {
  background: linear-gradient(to right, rgba(0,23,236,0.87) 0%,rgba(0,23,236,1) 50%,rgba(0,23,236,1) 100%);
}
#contact-form-header {
  margin: 0 auto;
  text-align: center;
  padding: 1em;
}
#contact-form-header h3 {
  padding-top: 0.15em;
  padding-bottom: 1em;
  color: #ffd500;
}
#contact-form-header p {
  font-family: 'Roboto', sans-serif;
  font-size: 0.75em;
  color: #fff;
}
#contact-form-field {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.input-form {
  width: 70%;
  padding: 1em;
  border: 0;
  border-bottom: 1px white solid;
  background-color: transparent;
}
.textarea-form {
  width: 70%;
  height: 150px;
  margin-top: 1em;
  padding: 1em;
  border: 1px white solid;
  background-color: transparent;
  resize: none;
}
#btn-group {
  margin-top: 1em;
}
.btn {
  text-transform: uppercase;
  padding: 0.5em 1em;
  margin: 0 2em;
  border: 1px white solid;
  background-color: transparent;
}
.btn-send {
  background-color: white;
  color: #0017EC;
}
.btn-cancel {
  color: white;
}
